<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <section class="py-4">
    <div class="mb-3 d-flex justify-content-between">
      <ul class="list-unstyled">
        <li class="nav-item dropdown">
          <a
            class="nav-link dropdown-toggle"
            href="#"
            role="button"
            data-bs-toggle="dropdown"
            aria-expanded="false"
          >
            篩選
          </a>
          <ul class="dropdown-menu">
            <li>
              <a
                class="dropdown-item"
                href="#"
                @click.prevent="selectOption.sort = 'asc'"
                >新->舊</a
              >
            </li>
            <li>
              <a
                class="dropdown-item"
                href="#"
                @click.prevent="selectOption.sort = ''"
                >舊->新</a
              >
            </li>
            <li><hr class="dropdown-divider" /></li>
            <li>
              <a
                class="dropdown-item"
                href="#"
                @click.prevent="selectOption.keyword = undefined"
                >所有</a
              >
            </li>
            <template v-for="(item, index) in companyList" :key="index">
              <li>
                <a
                  class="dropdown-item"
                  href="#"
                  @click.prevent="selectOption.keyword = item._id"
                  >{{ item.companyName }}</a
                >
              </li>
            </template>
          </ul>
        </li>
      </ul>
      <button
        class="btn btn-dark"
        @click="this.$emitter.emit('addEditOrder', { status: true })"
      >
        新增工單<i class="bi bi-plus-lg"></i>
      </button>
    </div>
    <table class="table table-striped table-hover">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">廠商</th>
          <th scope="col">付款方式</th>
          <th scope="col">案子金額</th>
          <th scope="col">施工地址</th>
          <th scope="col">施工日期</th>
          <!-- <th scope="col">工人名單</th> -->
          <th scope="col">狀態</th>
          <th scope="col">修改</th>
          <th scope="col">檢視</th>
        </tr>
      </thead>
      <tbody>
        <template v-for="(item, index) in orderList" :key="index">
          <tr>
            <th scope="row">{{ index + 1 }}</th>
            <td>{{ item.company.companyName }}</td>
            <td>{{ item.paymentMethod }}</td>
            <td>${{ this.$toCurrency(item.total) }}</td>
            <td>{{ item.address }}</td>
            <td>{{ item.constructionDate }}</td>
            <!-- <td>
              <table class="table mb-0">
                <thead class="table-light">
                  <tr>
                    <th scope="col">#</th>
                    <th scope="col">姓名</th>
                    <th scope="col">電話</th>
                    <th scope="col">工作時數</th>
                  </tr>
                </thead>
                <tbody>
                  <template v-for="(data, num) in item.workers" :key="num">
                    <tr>
                      <th scope="row">{{ num + 1 }}</th>
                      <td>{{ data._id.name }}</td>
                      <td>{{ data._id.phone }}</td>
                      <td>{{ data.time }}</td>
                    </tr>
                  </template>
                </tbody>
              </table>
            </td> -->
            <td>
              <button
                type="button"
                class="btn"
                :class="[item.status ? 'btn-success' : 'btn-outline-danger']"
                @click="updateStatus(item)"
              >
                <span v-if="item.status">已完成</span><span v-else>未完成</span>
              </button>
            </td>
            <td>
              <button
                class="btn btn-secondary"
                @click="
                  this.$emitter.emit('editOrder', { status: false, data: item })
                "
              >
                <i class="bi bi-pencil"></i>
              </button>
            </td>
            <td>
              <button
                class="btn btn-warning"
                @click="this.$emitter.emit('pdfEmit', item)"
              >
                <i class="bi bi-filetype-pdf"></i>
              </button>
            </td>
          </tr>
        </template>
      </tbody>
    </table>
  </section>
</template>

<script>
import {
  apiGetOrders,
  apiPatchOrderStatus,
  apiGetCompanys,
} from '@/assets/javascript/api';
import { getToken } from '@/assets/javascript/methods';

export default {
  data() {
    return {
      companyList: [],
      orderList: [],
      selectOption: {
        sort: 'asc',
        keyword: undefined,
      },
    };
  },
  watch: {
    // eslint-disable-next-line object-shorthand
    'selectOption.sort'() {
      this.getOrderList();
    },
    // eslint-disable-next-line object-shorthand
    'selectOption.keyword'() {
      this.getOrderList();
    },
  },
  methods: {
    checkSignIn() {
      // 取得 cookie 中的 token
      const token = getToken();

      // 若有 token 則登入，反之轉為登入頁面
      if (token) {
        this.$http.defaults.headers.common.Authorization = `Bearer ${token}`;
      } else {
        this.$router.push('/login');
      }
    },
    getOrderList() {
      this.$emitter.emit('loadingStatus', true);
      apiGetOrders(this.selectOption)
        .then((res) => {
          this.$emitter.emit('loadingStatus', false);
          this.orderList = res.data.data;
        })
        .catch((err) => {
          this.$emitter.emit('loadingStatus', false);
          console.log(err.response.data.message);
        });
    },
    updateStatus(status) {
      this.$emitter.emit('loadingStatus', true);
      apiPatchOrderStatus(status)
        .then(() => {
          this.getOrderList();
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    getCompanyList() {
      this.$emitter.emit('loadingStatus', true);
      apiGetCompanys()
        .then((res) => {
          this.$emitter.emit('loadingStatus', false);
          this.companyList = res.data.data;
        })
        .catch((err) => {
          this.$emitter.emit('loadingStatus', false);
          console.log(err);
        });
    },
  },
  mounted() {
    this.getOrderList();
    this.getCompanyList();
    this.$emitter.on('getOrder', () => {
      this.getOrderList();
    });
  },
  created() {
    this.checkSignIn();
  },
};
</script>
